import React from 'react'
import Stocks from './Stocks'
import ChartStock from './ChartStock'
// eslint-disable-next-line no-unused-vars
import { IStock } from './WSControl'

const ViewMonitor: React.FC = props => {
  // eslint-disable-next-line react/prop-types
  const { symbol, companyName, catchPhrase } = props.children as IStock
  return (
    <>
      <ChartStock>{symbol}</ChartStock>
      {companyName} - {catchPhrase}
      <Stocks>{symbol}</Stocks>

    </>
  )
}
export default ViewMonitor
