import React, { useEffect, useState, useRef } from 'react'
import { theme } from './ChartTheme'
import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official'

// eslint-disable-next-line no-unused-vars
import { useWs, WSSymbol } from './WSControl'

Highcharts.setOptions(theme)

const ChartStock: React.FC = (children) => {
  const { monitor, stockSymbols, isSubscribed } = useWs()
  const [stockHistoy, setStockHistory] = useState<number[]>([])

  const chartRef: any = useRef()
  const symbol = children.children as string
  const allowChartUpdate = true

  useEffect(() => {
    if (stockHistoy.length <= 100) {
      setStockHistory([...stockHistoy, [...[monitor.get(symbol)]]] as number[])
    } else {
      // const temp = stockHistoy
      // temp.shift()
      // temp.push(monitor.get(symbol) as number)
      chartRef.current.chart.series[0].addPoint(monitor.get(symbol) as number, true, true)
    }
  }, [monitor.get(symbol)])

  useEffect(() => {
    if (!(isSubscribed(symbol as WSSymbol))) {
      setStockHistory([])
    }
  },
  [stockSymbols])

  const options: Highcharts.Options = {
    series: [
      {
        type: 'line',
        data: stockHistoy,
        name: symbol as string
      }
    ],
    xAxis: {

      labels: {
        enabled: false
      }
    },
    chart: {
      animation: false,
      backgroundColor: {
        linearGradient: { x1: 0, x2: 0, y1: 1, y2: 0 },
        stops: [
          [0, 'rgb(0, 0, 0)'],
          [0.3, 'rgb(12, 12, 13)'],

          [1, 'rgb(33, 49, 75)']
        ]
      }

    },
    credits: { enabled: false },
    title: {
      text: ''
    }
  }
  return (
    <HighchartsReact
      highcharts={Highcharts}
      allowChartUpdate={allowChartUpdate}
      options={options}
      ref= {chartRef}
    />
  )
}

export default ChartStock
